.contact .popup__wrapper {
    background-color: $color-opal;
    padding: 0;
}

.contact__header {
    text-align: center;
    height: 100px;
    margin: 0;
    margin-bottom: 20px;

    color: $color-san-juan;
    font-size: 40px;
    font-weight: 600;
    line-height: 100px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;

    background: url(../img/logo-wall.jpg) no-repeat center center;
    background-size: 100%;
}

.contact__list {
    @include clear-list;
    display: flex;
    justify-content: space-around;
    margin-bottom: 10px;
    padding-bottom: 20px;

    border-bottom: 1px solid $color-san-juan;
}

.contact__item {
    padding: 20px;
    border: 1px solid $color-san-juan;
    border-radius: 15px;
}

.contact__tel,
.contact__address-head,
.contact__item--email {
    @include clear-link;
    font-style: normal;
    font-size: 20px;
}

.contact__tel,
.contact__item--email {
    line-height: 40px;
}

.contact__inner-wrap {
    display: flex;
    justify-content: space-between;
    padding: 0 20px 20px 20px;
}

.contact__adress {
    padding-top: 30px;
    padding-left: 20px;
    font-size: 20px;
}

.contact__form {

    display: flex;
    flex-direction: column;

    width: 60%;

    & input, textarea {
        border: none;
        border-radius: 5px;
    }
}

.contact__form-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 20px;

    & input {
        line-height: 30px;
        margin-top: 20px;
    }
}

.contact__form-header {
    text-align: center;
}

.contact__submit {
    margin-top: 20px;
    line-height: 30px;
    border: none;
    border-radius: 5px;
    background-color: $color-san-juan;
    color: white;
}

@media (max-width: $desktop-width) {
    .contact__list {
        flex-direction: column;
    }

    .contact__adress {
        display: none;
    }

    .contact__tel {
        display: block;
    }

    .contact__tel,
    .contact__address-head,
    .contact__item--email {
        font-size: 15px;
        text-align: center;
    }

    .contact__inner-wrap {
        justify-content: center;
    }
}