.about {
    padding-top: 120px;
}

.about__header {
    text-align: center;
}

.about__wrapper {
    display: flex;
}

.about__img {
    width: 100%;
}

.about__text {
    width: 650%;
    margin-top: 20px;
    margin-left: 10px;
}

@media (max-width: $desktop-width) {
    .about__wrapper {
        display: block;
    }

    .about__img {
        width: 100%;
    }
    
    .about__text {
        width: 90%;
        margin: 10px 0 0 40px;
        text-align: justify;
    }
}